import React from "react";
import { BrowserRouter as Router, Route, Redirect, Switch } from "react-router-dom";
import "./App.css";

/* components */
import Home from "./views/Home";
import Post from "./views/Post";
import User from "./views/User";
import Navigation from "./components/layouts/Navigation";
import { SignUp } from "./views/SignUp";
import SignIn from "./views/SignIn";
import Notifications from "./views/Notifications";
import PrivateMessages from "./views/PrivateMessages";

/* context */
import GlobalProvider from "./config/GlobalState/GlobalState";
import { withAuthentication } from "./config/Session";

/* Material UI*/
import Container from "@material-ui/core/Container";

/* routes */
import * as ROUTES from "./constants/routes";

import { HashRouter } from "react-router-dom";

const App = () => {
    return (
        <HashRouter>
            <GlobalProvider>
                <Navigation />
                <Container maxWidth="lg">
                    <Switch>
                        <Route exact path={ROUTES.HOME} component={Home} />
                        <Route exact path={ROUTES.SIGN_UP} component={SignUp} />
                        <Route exact path={ROUTES.SIGN_IN} component={SignIn} />
                        <Route exact path={ROUTES.USER} component={User} />
                        <Route exact path={ROUTES.POST} component={Post} />
                        <Route exact path={ROUTES.NOTIFICATIONS} component={Notifications} />
                        <Route exact path={ROUTES.MESSAGES} component={PrivateMessages} />
                        <Route exact path={`${ROUTES.MESSAGES}/:roomId`} component={PrivateMessages} />
                        <Route render={() => <Redirect to="/" />} />
                    </Switch>
                </Container>
            </GlobalProvider>
        </HashRouter>
    );
};

export default withAuthentication(App);
